@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  src: url(./fonts/Montserrat/Montserrat-Regular.ttf);
}
@font-face {
  font-family: 'Podkova-Bold';
  font-style: normal;
  src: url(./fonts/Podkova/static/Podkova-Bold.ttf)

}
@font-face {
  font-family: 'Podkova';
  font-style: normal;

  src: url(./fonts/Podkova/static/Podkova-ExtraBold.ttf)

}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  
}
ul {
  list-style: none;

}

a {
  text-decoration: none;
  color: inherit;
}
span {
  display: block;
}

.App {
  text-align: center;
  

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
